.title {
  margin: 2rem 0;
}

.error {
  border: 1px solid;
  margin: 10px auto;
  padding: 15px 10px 15px 50px;
  background-repeat: no-repeat;
  background-position: 10px center;
  max-width: 460px;
  color: #d8000c;
  background-color: #ffbaba;
  background-image: url('https://i.imgur.com/GnyDvKN.png');
}

.no-order {
  text-align: center;
}

.no-order a {
  font-size: 1.2rem;
  color: #19753f;
}

.notify {
  font-weight: bolder;
  font-size: 2rem;
}
